/**
 * RuToken simplified library
 * @author Aleksandr.ru
 * @link http://aleksandr.ru
 */

import rutoken from 'rutoken';
import DN from '../DN';
import errors from './errors';
import { convertDN, stripDnQuotes } from '../helpers';

const requiredFunctions = [
	'cmsDecrypt',
	'cmsEncrypt',
	'createPkcs10',
	'deleteCertificate',
	'deleteKeyPair',
	'enumerateCertificates',
	'enumerateDevices',
	'enumerateKeys',
	'generateKeyPair',
	'getCertificate',
	'getCertificateInfo',
	'getDeviceInfo',
	'getDeviceModel',
	'getDeviceType',
	'getKeyByCertificate',
	'importCertificate',
	'login',
	'logout',
	'parseCertificate',
	'sign',
	'verify'
];

function RuToken() {
	var plugin, deviceId;

	/**
	 *      
	 * @returns {Promise<Object>} ,   
	 */
	this.init = function(){
		return rutoken.ready.then( _ => {
			if (window.chrome) {
				return rutoken.isExtensionInstalled();
			} else {
				return Promise.resolve(true);
			}
		}).then(result => {
			if (result) {
				return rutoken.isPluginInstalled();
			} else {
				throw new Error("Rutoken Extension wasn't found");
			}
		}).then(result => {
			if (result) {
				return rutoken.loadPlugin();
			} else {
				throw new Error("Rutoken Plugin wasn't found");
			}
		}).then(result => {
			const absentFn = requiredFunctions.filter(fn => !result[fn]);
			if (absentFn.length) {
				console.log('Missing plugin functions: ', absentFn);
				throw new Error(" , ,            2.0");
			}
			//    
			plugin = result;
			return plugin.enumerateDevices();
		}).then(devices => {
			const len = devices.length;
			if (len === 1) {
				deviceId = devices.shift();
			}
			else if(len === 0) {
				throw new Error("   ");
			}
			else if(len > 1) {
				throw new Error(' ' + len + ' ');
			}
			return Promise.all([
				plugin.getDeviceInfo(deviceId, plugin.TOKEN_INFO_READER), //   
				plugin.getDeviceInfo(deviceId, plugin.TOKEN_INFO_LABEL), //   					
				plugin.getDeviceInfo(deviceId, plugin.TOKEN_INFO_SERIAL), //   
				plugin.getDeviceModel(deviceId),
				plugin.getDeviceType(deviceId)
			]);
		}).then(infos => {
			return {
				version: plugin.version,
				serial: infos[2],
				reader: infos[0],
				label: infos[1].indexOf('Rutoken ECP <no label>') + 1 ? '' : infos[1],
				type: infos[4],
				model: infos[3]
			};
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *     - 
	 * @param {string} userPin  ,      prompt
	 * @returns {Promise}
	 */
	this.bind = function(userPin) {
		return new Promise((resolve, reject) => {
			plugin.getDeviceInfo(deviceId, plugin.TOKEN_INFO_IS_LOGGED_IN).then(result => {
				if(result) {
					resolve('  ');
					return false; // no need to log in
				}
				else {
					if (!userPin) {
						userPin = prompt('PIN-   ');
						if (!userPin) throw new Error('    ');
					}
				}
				return true; // need to log in 
			}).then(needToLogIn => {
				if (needToLogIn) {
					return plugin.login(deviceId, userPin).then( _ => {
						resolve(true);
					});
				}
				else {
					resolve(true);
				}
			}).then(null, e => {
				const err = getError(e);
				reject(err);
			});
		});
	};

	/**
	 *   PIN-.      
	 * @returns {Promise}
	 */
	this.unbind = function() {
		return new Promise((resolve, reject) => {
			plugin.getDeviceInfo(deviceId, plugin.TOKEN_INFO_IS_LOGGED_IN).then(result => {
				if(!result) {
					resolve('  ');
					return false; // no need to log out
				}
				else {
					return true; // need to log out 
				}
			}).then(needToLogOut => {
				if (needToLogOut) {
					return plugin.logout(deviceId).then( _ => {
						resolve(true);
					});
				}
				else {
					return true;
				}
			}).then(null, e => {
				const err = getError(e);
				reject(err);
			});
		});
	};

	/**
	 *   (    )
	 * @returns {Promise<number>}   
	 */
	this.clean = function(){
		let count = 0;
		return plugin.enumerateCertificates(deviceId, plugin.CERT_CATEGORY_USER).then(results => {
			let promises = [];
			for (let i in results) {
				const certId = results[i];
				promises.push(plugin.deleteCertificate(deviceId, certId));
			}
			count += promises.length;
			return Promise.all(promises);
		}).then(() => {
			const marker = ''; //   , "" -  
			return plugin.enumerateKeys(deviceId, marker);
		}).then(results => {
			let promises = [];
			for (let i in results) {
				const keyId = results[i];
				promises.push(plugin.deleteKeyPair(deviceId, keyId));
			}
			count += promises.length;
			return Promise.all(promises);
		}).then(() => {
			return count;
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *    
	 * @param {DN} dn
	 * @param {string} marker   
	 * @param {array} extKeyUsage  OID Extended Key Usage, -   '1.3.6.1.5.5.7.3.2' +    '1.3.6.1.5.5.7.3.4'
	 * @param {string} algorithm  "PUBLIC_KEY_ALGORITHM_GOST3410_2012_256" (-)  "PUBLIC_KEY_ALGORITHM_GOST3410_2001".
	 * @returns {Promise<Object>}    { csr: 'base64   ', containerId }
	 * @see DN
	 */
	this.generateCSR = function(dn, marker, extKeyUsage, algorithm){
		let keyId = '';
		if (!marker) {
			marker = '';
		}
		if (!extKeyUsage || !extKeyUsage.length) {
			extKeyUsage = [
				'clientAuth', // 1.3.6.1.5.5.7.3.2', //  
				'emailProtection', // '1.3.6.1.5.5.7.3.4' //   
			];
		}
		const publicKeyAlgorithm = algorithm && plugin[algorithm] || plugin.PUBLIC_KEY_ALGORITHM_GOST3410_2012_256;
		let paramset = 'XA';
		let hashAlgorithm = plugin.HASH_TYPE_GOST3411_94;
		if (publicKeyAlgorithm === plugin.PUBLIC_KEY_ALGORITHM_GOST3410_2012_512) {
			paramset = 'A';
			hashAlgorithm = plugin.HASH_TYPE_GOST3411_12_512;
		} 
		else if (publicKeyAlgorithm === plugin.PUBLIC_KEY_ALGORITHM_GOST3410_2012_256) {
			hashAlgorithm = plugin.HASH_TYPE_GOST3411_12_256;
		}
		const reserved = undefined;
		const options = {
			publicKeyAlgorithm,
			paramset
		};
		return plugin.generateKeyPair(deviceId, reserved, marker, options).then(result => {
			keyId = result;
			let subject = [];
			for (let i in dn) if(dn.hasOwnProperty(i)) {
				subject.push({
					rdn: i,
					value: dn[i]
				});
			}
			const keyUsage = [
				"digitalSignature"
				,"nonRepudiation"
				,"keyEncipherment"
				,"dataEncipherment"
			];
			const extensions = {
				keyUsage,
				extKeyUsage
			};
			const subjectSignTool = ' " "';
			const options = {
				subjectSignTool,
				hashAlgorithm
			};
			return plugin.createPkcs10(deviceId, keyId, subject, extensions, options);
		}).then(result => {
			return {
				csr: cleanPemString(result),
				keyPairId: keyId
			};
		}).then(null, e => {
			if(keyId) {
				plugin.deleteKeyPair(deviceId, keyId);
			}
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *    
	 * @param {string} certificate base64(       DER)
	 * @returns {Promise}
	 */
	this.writeCertificate = function(certificate){
		const category = plugin.CERT_CATEGORY_USER;
		return plugin.importCertificate(deviceId, certificate, category).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *    .
	 * @param {int} certId  
	 * @returns {Promise<Object>}
	 */
	this.certificateInfo = function(certId){
		let hasPrivateKey = false;
		let serialNumber = '';
		return new Promise(resolve => {
			plugin.getKeyByCertificate(deviceId, certId).then(keyId => {
				resolve(!!keyId);
			}).then(null, e => {
				const err = getError(e);
				console.log('getKeyByCertificate', certId, err);
				resolve(false);
			});
		}).then(result => {
			hasPrivateKey = result;
			return plugin.getCertificateInfo(deviceId, certId, plugin.CERT_INFO_SERIAL_NUMBER);
		}).then(result => {
			serialNumber = result;
			return plugin.parseCertificate(deviceId, certId);
		}).then(o => {
			const ver = o.text.match(/Version: (\d+)/);
			const version = ver.length > 1 ? ver[1] : undefined;
			const algo = o.text.match(/Public Key Algorithm: (.+)$/m);
			const keyAlgorithm = algo.length > 1 ? algo[1] : '';
			const dn = makeDN(o.subject);
			const dnI = makeDN(o.issuer);
			const dt = new Date();
			const info = {
				Name: dn.commonName || dn.CN,
				Issuer: dnI,
				IssuerName: stripDnQuotes(dnI.toString()),
				Subject: dn,
				SubjectName: stripDnQuotes(dn.toString()),
				Version: version,
				Algorithm: keyAlgorithm,
				SerialNumber: serialNumber,
				Thumbprint: certId.replace(/\:/g, ''),
				ValidFromDate: new Date(o.validNotBefore),
				ValidToDate: new Date(o.validNotAfter),
				HasPrivateKey: hasPrivateKey,
				IsValid: dt >= new Date(o.validNotBefore) && dt <= new Date(o.validNotAfter),
				//ProviderName: '', //TODO
				//ProviderType: undefined, //TODO
				toString: function(){
					return ':              ' + this.Name +
						'\n:              ' + this.IssuerName +
						'\n:               ' + this.SubjectName +
						'\n:                ' + this.Version +
						'\n:              ' + this.Algorithm + // PublicKey Algorithm
						'\n :            ' + this.SerialNumber +
						'\n SHA1:        ' + this.Thumbprint +
						'\n  :    ' + this.ValidFromDate +
						'\n  : ' + this.ValidToDate +
						'\n :        ' + (this.HasPrivateKey ? '' : ' (   )') +
						'\n:              ' + (this.IsValid ? '' : '');
				}
			};
			return info;
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *    
	 * @returns {Promise<Array>} [{id, subject}, ...]
	 */
	this.listCertificates = function(){
		let certIds = [];
		let certs = [];
		return plugin.enumerateCertificates(deviceId, plugin.CERT_CATEGORY_USER).then(results => {
			certIds = results;
			let promises = [];
			for (let i in certIds) {
				promises.push(plugin.parseCertificate(deviceId, certIds[i]));
			}
			return Promise.all(promises);
		}).then(results => {
			for (let i in certIds) {
				const dn = makeDN(results[i] && results[i].subject);
				certs.push({
					id: certIds[i],
					name: formatCertificateName(dn)
				});
			}
			return certs;
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};
	
	/**
	 *  
	 * @param {string} certId 
	 * @returns {Promise<string>} base64(       DER)
	 */
	this.readCertificate = function(certId){
		return plugin.getCertificate(deviceId, certId).then(result => {
			return cleanPemString(result);
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *  .     PKCS#7,    Base64
	 * @param {string} dataBase64      base64
	 * @param {string} certId  
	 * @param {object} [options]
	 * @param {boolean} [options.attached]  
	 * @returns {Promise<string>} -   PKCS#7,   Base64.
	 */
	this.signData = function(dataBase64, certId, options){
		if (!options) options = {};
		const { attached } = options;
		return plugin.sign(deviceId, certId, dataBase64, plugin.DATA_FORMAT_BASE64, {
			detached: !attached
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *    .
	 * @param {string} dataBase64    
	 * @param {string} signBase64  
	 * @param {string} certId  
	 * @param {object} [options]
	 * @param {boolean} [options.attached]  
	 * @returns {Promise<string>} base64
	 */
	this.addSign = function(dataBase64, signBase64, certId, options){
		if (!options) options = {};
		const { attached } = options;
		if (attached) {
			//   CMS  ,   data   
			dataBase64 = '';
		}
		return plugin.sign(deviceId, certId, dataBase64, plugin.DATA_FORMAT_BASE64, {
			detached: !attached,
			CMS: signBase64
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *  .
	 * @param {string} dataBase64    
	 * @param {string} signBase64  
	 * @param {object} [options]
	 * @param {boolean} [options.attached]  
	 * @returns {Promise<boolean>} true  reject
	 */
	this.verifySign = function(dataBase64, signBase64, options){
		if (!options) options = {};
		const { attached } = options;
		const args = {
			base64: true,
			verifyCertificate: false
		};
		if (!attached) {
			args.data = dataBase64;
		}
		return plugin.verify(deviceId, signBase64, args).then(result => {
			if (!result) {
				//    -  
				throw new Error('  ');
			}
			return true;
		}).then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *  
	 * @param {string} dataBase64   base64
	 * @param {string} certId  
	 * @returns {Promise<string>} base64 enveloped data
	 */
	this.encryptData = function(dataBase64, certId) {
		return plugin.getCertificate(deviceId, certId)
		.then(cert => plugin.cmsEncrypt(deviceId, "", [cert], dataBase64, {
			base64: true
		}))
		.then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *  
	 * @param {string} dataBase64   base64
	 * @param {string} certId  
	 * @returns {Promise<string>} base64
	 */
	this.decryptData = function(dataBase64, certId) {
		return plugin.getKeyByCertificate(deviceId, certId)
		.then(keyId => plugin.cmsDecrypt(deviceId, keyId, dataBase64, {
			base64: true
		}))
		.then(null, e => {
			const err = getError(e);
			throw new Error(err);
		});
	};

	/**
	 *    
	 * @param {string|Error} e    
	 * @returns {string}  
	 */
	function getError(e) {
		const ee = e.message && e.message.match(/^[0-9]+$/) && e.message || e;
		let mnemo = '';
		if (plugin) for(let i in plugin.errorCodes) {
			if (plugin.errorCodes[i] == ee) {
				mnemo = i;
				break;
			}
		}
		return mnemo && errors[mnemo] || mnemo || e.message || e;
	}

	/**
	 *  DN   [{rdn: ..., value: ...}, ...]
	 * @param {[index: number]: { rdn: string, value: string }} obj 
	 * @returns {DN}
	 */
	function makeDN(obj)
	{
		var dn = new DN;
		for(var i in obj) {
			var rdn = obj[i].rdn;
			var val = obj[i].value;
			if (rdn && val) {
				dn[rdn] = val;
			}
		}
		return convertDN(dn);
	}

	/**
	 *   
	 * @param {DN} o ,      .
	 * @returns {string} 
	 */
	function formatCertificateName(o)
	{
		return '' + o['CN']
			+ (o['INNLE'] ? ';   ' + o['INNLE'] : '')
			+ (o['INN'] ? ';  ' + o['INN'] : '')
			+ (o['SNILS'] ? ';  ' + o['SNILS'] : '');
	}

	/**
	 *     PEM,   base64 
	 * @param {String} pem 
	 * @returns {String}
	 */
	function cleanPemString(pem) {
		return pem.replace(/^-+(BEGIN|END)[^-]+-+$/gm, '').trim();
	}
}

export default RuToken;
